import React, { Component } from 'react'
import {BrowserRouter, Route } from 'react-router-dom'
import ReactDOM from 'react-dom'
import TrashBin from './trashBin/trashbin.js'


//Components 

import Home from './home/home.js'
import Reminders from './reminders/reminders.js'
import Header from './page-componentss/header/header.js'
import Sidebar from './page-componentss/sidebar/sidebar.js'
import JSON from './home/notes.json'


class App extends Component {
   constructor(){
      super()
      this.state = {
         sidebar:'closed',
         JSONnotes:JSON,
         trashArr:[],
         pushed:'',
         deketed:''
   }
   this.pushTrash = ( item ) => {
      this.state.trashArr.push (item )
      this.setState({
         pushed:'Ok'
      })
   } 
   this.state.JSONnotes.sort()
   this.clearTrash = () => {
      var length = this.state.trashArr.length
      this.state.trashArr.splice(0,length);
      this.setState({
         deleted:length
      })
  }

   this.sidebarUpdate = (value) => {
      this.setState({
         sidebar:value
      })
   }
   this.deleteOneTrash = (index) => {
      this.state.trashArr.splice(index,1)
      this.setState({
         deleted:index
      })
   }




   }
   


   

   render(){
      console.log(this.state.JSONnotes)
      return (
         <BrowserRouter>
            <Header sidebar = {this.state.sidebar} sidebarUpdate = {this.sidebarUpdate}/>
            <Sidebar sidebar = {this.state.sidebar} sidebarUpdate = {this.sidebarUpdate}/>
            <Route path='/' exact component={() => <Home pushTrash = {this.pushTrash} JSONnotes = {this.state.JSONnotes} />} />
            <Route path='/reminders' component = {Reminders} />
            <Route path='/bin' component={() => <TrashBin clearTrash = {this.clearTrash} deleteOneTrash = {this.deleteOneTrash} trashArr = {this.state.trashArr} JSONnotes = {this.state.JSONnotes} />} />
            {/* <Route path='/' component = 'Home' /> */}
            {/* <Footer/>            */}
         
         
         
         </BrowserRouter>
      )
   }

}

ReactDOM.render(<App/>,document.getElementById('root'))