import React, { Component } from 'react';


//Components
import InputNotes from './inputNote'



class Home extends Component {


   
   
     
   
  
   render(){
   
      return (
         <div>
            <InputNotes {...this.props} JSONnotes={this.props.JSONnotes}/>
            
         </div>
         
      )
   }
   }



export default Home;